import Vue from 'vue'
import Vuex from 'vuex'
import axios from "../axios";
import {promise} from "ora";
Vue.use(Vuex)

export default new Vuex.Store({
  // State lÃ  Tráº¡ng thÃ¡i => hay cÃ²n gá»i lÃ  Data (dá»¯ liá»‡u) cá»§a KHO chá»©a (store)
  // Táº¥t cáº£ dá»¯ liá»‡u sáº½ Ä‘Æ°á»£c khá»Ÿi táº¡o trong thuá»™c tÃ­nh `state`
  state: {
    storeIsLogged: false,
    storeUserData: {
      id: '',
      name: '',
      email: ''
    }
  },

  // Getters: lÃ  cÃ¡c thuá»™c tÃ­nh dÃ¹ng Ä‘á»ƒ cÃ¡c component láº¥y dá»¯ liá»‡u tá»« KHO chá»©a (store) vá»
  getters: {
  },

  // Mutations: lÃ  cÃ¡c thuá»™c tÃ­nh dÃ¹ng Ä‘á»ƒ thay Ä‘á»•i giÃ¡ trá»‹ bÃªn trong KHO chá»©a (store)
  // thay Ä‘á»•i giÃ¡ trá»‹, nhÆ°ng cháº¡y Äá»’NG Bá»˜ (SYNC)
  mutations: {
    checkStoreIsLogged(state, value) {
      state.storeIsLogged = value
    },
    checkStoreUserData(state, value) {
      state.storeUserData.id = value.id || ''
      state.storeUserData.name = value.name || ''
      state.storeUserData.email = value.email || ''
    }
  },

  // Actions: lÃ  cÃ¡c hÃ m (methods) dÃ¹ng Ä‘á»ƒ thá»±c hiá»‡n cÃ¡c hÃ nh Ä‘á»™ng thay Ä‘á»•i giÃ¡ trá»‹ bÃªn trong KHO chá»©a (store)
  // tÆ°Æ¡ng tá»± nhÆ° Mutations, nhÆ°ng cÃ³ thá»ƒ cháº¡y Báº¤T Äá»’NG Bá»˜ (ASYNC)
  actions: {
    setIsLogged (context, value) {
      context.commit('checkStoreIsLogged', value)
    },
    setUserData (context, value) {
      context.commit('checkStoreUserData', value)
    },
    initBeforRouter (context) {
      let dataUser = {}
      let isLogged = false
      let logged = localStorage.getItem('isLogged')
      let user = localStorage.getItem('user')
      if (user != undefined && user !== '') {
        dataUser = JSON.parse(user)
      }
      isLogged = logged != null && logged !== 'false'
      context.commit('checkStoreUserData', dataUser)
      context.commit('checkStoreIsLogged', isLogged)
    },
    initStoreStateBeforeCreate (context) {
      return new Promise((resolve, reject) => {
        axios.get('/check-token')
          .then(response => {
            let dataUser = {}
            let isLogged = false
            if (response.data.code == 200) {
              let logged = localStorage.getItem('isLogged')
              let user = localStorage.getItem('user')
              if (user != undefined && user !== '') {
                dataUser = JSON.parse(user)
              }
              isLogged = logged != null && logged !== 'false'
            }
            context.commit('checkStoreUserData', dataUser)
            context.commit('checkStoreIsLogged', isLogged)
            resolve(response)
          })
          .catch(error => {
            context.commit('checkStoreUserData', {})
            context.commit('checkStoreIsLogged', false)
            reject(error)
          })
      })
    }
  }
})



